<template>
  <div class="share-panel">
    <a-upload-dragger name="file" multiple action="//jsonplaceholder.typicode.com/posts/">
      <p class="ant-upload-drag-icon">
        <a-icon type="cloud-upload"/>
        <span>上传模型</span>
      </p>
      <p class="ant-upload-text">将STL/STP/STEP格式的文件直接拖拽上传 或 直接点击上传</p>
      <p class="ant-upload-hint">批量上传建议＜10个，FDM工艺请将单个数据尺寸控制在160x160x160mm以内</p>
    </a-upload-dragger>
    <a-row :gutter="16" style="margin: 16px;">
      <a-col :span="6">
        <a-steps direction="vertical" size="small" :current="0">
          <a-step title="上传模型"/>
          <a-step title="选择工艺和材料"/>
          <a-step title="智能报价"/>
          <a-step title="在线支付"/>
          <a-step title="3D打印制造"/>
          <a-step title="后处理工艺"/>
          <a-step title="包装物流配送"/>
        </a-steps>
      </a-col>
      <a-col :span="18">
        <a-table
          :rowSelection="rowSelection"
          :columns="columns"
          :dataSource="data"
          bordered
          :pagination="false"
        >
          <div slot="num" slot-scope="text, record">
            <a-input-number size="small" :min="0" v-model="record.num"/>
          </div>
          <div slot="footer" style="text-align: right;">
            <span class="footer-info">
              已选
              <span class="special-txt">{{buyNum}}</span>件商品
            </span>
            <span class="footer-info">
              合计:
              <span
                class="special-txt"
                style="font-size: 2em; font-weight: bold;"
              >￥{{totalMoney}}</span>
            </span>
            <a-button type="primary" style="margin-right: 0.6em;" @click="chooseTech">选择工艺</a-button>
            <a-button>立即下单</a-button>
          </div>
        </a-table>
      </a-col>
    </a-row>
    <choose-tech ref="chooseTech"/>
  </div>
</template>

<script>
import ChooseTech from '@/components/print/ChooseTech'

export default {
  name: 'share',
  components: { ChooseTech },
  data () {
    const columns = [{
      title: '文件信息',
      dataIndex: 'fileInfo'
    }, {
      title: '打印信息',
      dataIndex: 'printInfo',
      width: '30%'
    }, {
      title: '单价',
      dataIndex: 'price',
      width: '12%'
    }, {
      title: '数量',
      dataIndex: 'num',
      width: '12%',
      scopedSlots: { customRender: 'num' }
    }]

    const data = [{
      key: 1,
      fileInfo: '文件信息',
      printInfo: '打印信息',
      price: 60,
      num: 2
    }, {
      key: 2,
      fileInfo: '文件信息',
      printInfo: '打印信息',
      price: 60,
      num: 2
    }]
    return {
      data,
      selectedGoods: [],
      columns,
      rowSelection: {
        onChange: (selectedRowKeys, selectedRows) => {
          this.selectedGoods = selectedRows
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
        }
      }
    }
  },
  computed: {
    /**
     * 购买的数量
     */
    buyNum () {
      return this.selectedGoods.length
    },
    /**
     * 合计金额
     */
    totalMoney () {
      return this.buyNum === 0 ? 0 : this.selectedGoods.reduce((pre, cur) => (pre + cur.price * cur.num), 0)
    }
  },
  methods: {
    /**
     * 多选时回调
     * @param {object} selectedRowKeys:  选择行的 key
     * @param {object} selectedRows: 选择的行
     */
    selectRows (selectedRowKeys, selectedRows) {
      this.selectedGoods = selectedRows
    },
    /**
     * 选择工艺
     */
    chooseTech () {
      this.$refs.chooseTech.showModal()
    }
  }
}
</script>

<style scoped>
.share-panel {
  background-color: #e6e6e6;
}
.footer-info {
  margin-right: 1em;
}
.special-txt {
  margin: 0 0.4em;
  color: #ff653f;
}
</style>
