<template>
  <a-modal
    title="选择工艺"
    :visible="visible"
    @ok="handleOk"
    :confirmLoading="confirmLoading"
    @cancel="handleCancel"
  >
    <a-form layout="horizontal" size="small">
      <a-form-item
        label="材料选择"
        :label-col="formItemLayout.labelCol"
        :wrapper-col="formItemLayout.wrapperCol"
      >
        <a-cascader size="small" :options="techOptions" placeholder="请选择材料"/>
      </a-form-item>
      <a-form-item
        label="材料精度"
        :label-col="formItemLayout.labelCol"
        :wrapper-col="formItemLayout.wrapperCol"
      >
        <a-radio-group default-value="101" size="small">
          <a-radio-button value="101">普通（101~400μm）</a-radio-button>
          <a-radio-button value="91">精细（91~100μm）</a-radio-button>
        </a-radio-group>
      </a-form-item>
      <a-form-item
        label="手机号"
        :label-col="formItemLayout.labelCol"
        :wrapper-col="formItemLayout.wrapperCol"
      >
        <a-input placeholder="输入手机号以获取报价" size="small"/>
      </a-form-item>
    </a-form>
  </a-modal>
</template>
<script>
export default {
  data () {
    const techOptions = [{
      value: 'SLM',
      label: '金属（SLM）',
      children: [{
        value: '316L',
        label: '不锈钢316L'
      }, {
        value: '模具钢',
        label: '模具钢'
      }]
    }, {
      value: 'FDM',
      label: 'PLA（FDM）',
      children: [{
        value: 'PLA打印丝',
        label: 'PLA打印丝'
      }]
    }]
    return {
      techOptions,
      visible: false,
      confirmLoading: false,
      formItemLayout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 14 }
      }
    }
  },
  methods: {
    /**
     * 打开弹出框
     */
    showModal () {
      this.visible = true
    },
    /**
     * 确定操作
     */
    handleOk (e) {
      this.ModalText = 'The modal will be closed after two seconds'
      this.confirmLoading = true
      setTimeout(() => {
        this.visible = false
        this.confirmLoading = false
      }, 2000)
    },
    /**
     * 取消操作
     */
    handleCancel (e) {
      this.visible = false
    }
  }
}
</script>
